/* regular*/
@font-face {
  font-family: "Myriad Pro";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Myriad Pro Regular"), url('../fonts/myriad/MyriadPro-Semibold.woff') format('truetype');
}
body {
  margin: 0;
  padding: 0;
  font-family: "Myriad Pro Regular", "Arial", sans-serif;
  font-size: 14px;
  line-height: 1;
  font-weight: 400;
  color: #196fc0;
  text-transform: none;
  background-color: #FFFFFF;
}
@media (max-width: 600px) {
  body {
    line-height: 16px;
  }
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}
/* img {
  max-width: 100%;
  height: auto;
} */
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  max-width: 1210px;
  margin: 0 auto;
  padding: 100px 20px;
}
@media (max-width: 600px) {
  .container {
    padding: 50px 10px;
  }
}
.wrapper {
  max-width: 1210px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 600px) {
  .wrapper {
    padding: 0 10px;
  }
}
.introduce {
  background-image: url("../img/main-img.png");
  background-color: #000000;
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 100vh;
  color: #FFFFFF;
}
@media (max-width: 950px) {
  .introduce {
    min-height: 100%;
  }
}
/* .introduce__logo {
  margin-top: 135px;

} */
.introduce__text {
  text-align: center;
  margin-top: 100px;
}
@media (max-width: 950px) {
  .introduce__text {
    margin-top: 50px;
  }
}
.introduce__text img {
  margin-bottom: 30px;
}
.introduce__h1 {
  font-size: 40px;
  margin-bottom: 30px;
}
@media (max-width: 950px) {
  .introduce__h1 {
    font-size: 22px;
    margin-bottom: 10px;
  }
}
@media (max-width: 600px) {
  .introduce__h1 {
    font-size: 18px;
  }
}
.introduce__who {
  font-size: 18px;
  margin-bottom: 20px;
}
@media (max-width: 950px) {
  .introduce__who {
    margin-bottom: 10px;
  }
}
@media (max-width: 600px) {
  .introduce__who {
    font-size: 14px;
  }
}
.introduce__h2 {
  font-size: 30px;
  margin-bottom: 50px;
}
@media (max-width: 950px) {
  .introduce__h2 {
    font-size: 20px;
    margin-bottom: 30px;
  }
}
@media (max-width: 600px) {
  .introduce__h2 {
    font-size: 18px;
  }
}
.introduce__about {
  width: 700px;
  font-size: 14px;
  margin: 0 auto;
  line-height: 1.5;
}
@media (max-width: 950px) {
  .introduce__about {
    width: auto;
  }
}
@media (max-width: 600px) {
  .introduce__about {
    font-size: 12px;
  }
}
.navigation {
  background-color: #196fc0;
  color: #FFFFFF;
}
.container.navigation__container {
  padding: 0 20px;
}
.navigation__container {
  display: flex;
  justify-content: space-between;
}
.navigation__box::before {
  content: "";
  display: block;
  width: 40px;
  height: 3px;
  background-color: #FFFFFF;
  box-shadow: 0 7px 0px 0px rgb(255 255 255), 0 14px 0px 0px rgb(255 255 255);
  margin: 15px 0;
}
@media (min-width: 950px) {
  .navigation__box::before {
    display: none;
  }
}
.navigation__list {
  display: flex;
  list-style: none;
  text-transform: uppercase;
  font-size: 18px;
  padding: 0;
  margin: 0;
  margin-top: 10px;
}
@media (max-width: 950px) {
  .navigation__list {
    flex-direction: column;
    display: none;
  }
  .navigation__list.active {
    display: block;
  }
}
.navigation__item {
  padding-left: 20px;
  position: relative;
}
.navigation__item.active span::after {
  content: "";
  position: absolute;
  background-color: #FFFFFF;
  width: 80%;
  height: 2px;
  bottom: 0;
}
.navigation__item.active span {
  padding-bottom: 17px;
  display: flex;
}
.navigation__logo {
  height: 100%;
  margin: 14px 0 14px;
}
.welcome {
  text-align: center;
}
.welcome__list {
  list-style: none;
  padding: 0;
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
}
@media (max-width: 950px) {
  .welcome__list {
    grid-template-rows: none;
    grid-template-columns: none;
  }
}
.welcome__item {
  color: #000000;
  display: flex;
  /*   &::before::nth-child(3){
    content: "123";
    width: 50px;
    height: 50px;
    display: block;
  } */
}
@media (max-width: 950px) {
  .welcome__item {
    align-items: center;
  }
}
.welcome__item-center {
  grid-row-start: 1;
  grid-row-end: 3;
  grid-column-start: 2;
  display: flex;
  justify-content: center;
  position: relative;
}
@media (max-width: 950px) {
  .welcome__item-center {
    grid-row-end: 2;
    grid-column-start: 1;
  }
}
.welcome__item-right {
  padding-top: 20px;
  flex-direction: row-reverse;
}
.welcome__item-left {
  padding-top: 20px;
}
.welcome__item-right .welcome__box {
  text-align: end;
  margin-right: 15px;
}
.welcome__item .welcome__text {
  font-size: 12px;
}
.welcome__item-left .welcome__box {
  margin-left: 15px;
}
.welcome__item-center::before {
  content: "";
  position: absolute;
  background-color: #cccccc;
  width: 70px;
  height: 1px;
  top: 50%;
  left: -85px;
}
@media (max-width: 950px) {
  .welcome__item-center::before {
    left: 50px;
  }
}
@media (max-width: 600px) {
  .welcome__item-center::before {
    display: none;
  }
}
.welcome__item-center::after {
  content: "";
  position: absolute;
  background-color: #cccccc;
  width: 70px;
  height: 1px;
  top: 50%;
  right: -75px;
}
@media (max-width: 950px) {
  .welcome__item-center::after {
    right: 50px;
  }
}
@media (max-width: 600px) {
  .welcome__item-center::after {
    display: none;
  }
}
.welcome__item-left::before,
.welcome__item-right::before {
  content: "";
  background: url(../img/welcome-star.png) no-repeat;
  background-size: initial;
  width: 165px;
  height: 80px;
  display: block;
}
@media (max-width: 950px) {
  .welcome__item-left::before,
  .welcome__item-right::before {
    background-size: contain;
    background-position-y: center;
  }
}
@media (max-width: 950px) {
  .welcome__item-right::before {
    background-position-x: right;
  }
  .welcome__item-left::before {
    background-position-x: left;
  }
  .welcome__item-center img {
    width: 200px;
  }
}
@media (max-width: 600px) {
  .welcome__item-center img {
    width: 100px;
  }
}
.examples {
  background-image: url("../img/work.png");
  background-color: #000000;
  background-size: cover;
  background-repeat: no-repeat;
  color: #FFFFFF;
  line-height: 42px;
}
@media (max-width: 600px) {
  .examples {
    line-height: 24px;
  }
}
.examples__title {
  font-size: 16px;
  text-align: center;
  padding: 10px 10px 15px;
}
@media (max-width: 600px) {
  .examples__title {
    font-size: 10px;
  }
}
.examples__text {
  font-size: 36px;
  text-align: center;
}
@media (max-width: 600px) {
  .examples__text {
    font-size: 16px;
  }
}
.examples__text span {
  color: #196fc0;
}
.examples__btn {
  margin: 100px auto 0;
  width: 200px;
  display: block;
  background-color: #196fc0;
  padding: 20px;
  color: #FFFFFF;
  border: none;
  cursor: pointer;
}
.examples__btn:hover {
  background-color: #004b91;
  transition: 0,5s;
}
@media (max-width: 600px) {
  .examples__btn {
    width: 156px;
    padding: 10px;
    margin: 50px auto 0;
  }
}
.about {
  text-align: center;
}
.about__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
@media (max-width: 600px) {
  .about__list {
    font-size: 22px;
    margin-top: 0;
  }
}
.about__item {
  padding: 10px;
  color: #000000;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.about__item:hover h3,
.about__item:hover a {
  color: #196fc0;
}
.about__item:hover .main-icon__box {
  background-color: #196fc0;
  box-shadow: none;
  transition: 1s;
}
.about__item:hover .main-icon__cont {
  color: #FFFFFF;
}
.about__item:hover .main-icon__text {
  color: #196fc0;
}
.about__item:hover .about-icon1 {
  fill: #FFFFFF;
}
@media (max-width: 600px) {
  .about__item img {
    width: 50px;
    height: auto;
  }
}
.about__item a {
  color: #eeeeee;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 600px) {
  .about__item a {
    font-size: 10px;
  }
}
.about__item p {
  max-width: 282px;
}
@media (max-width: 600px) {
  .about__item p {
    font-size: 10px;
  }
}
@media (max-width: 600px) {
  .about__item h3 {
    margin: 0;
    font-size: 12px;
  }
}
.skills {
  color: #000000;
  background-color: #eeeeee;
}
.skills__container {
  display: flex;
}
@media (max-width: 600px) {
  .skills__container {
    flex-direction: column;
    align-items: center;
  }
}
.skills iframe {
  width: 100%;
}
.skills__container div:first-child {
  padding-right: 70px;
}
@media (max-width: 900px) {
  .skills__container div:first-child {
    padding-right: 20px;
  }
}
@media (max-width: 600px) {
  .skills__container div:first-child {
    padding-right: 0;
  }
}
.skills__video,
.skills__procients {
  width: 50%;
}
@media (max-width: 600px) {
  .skills__video,
  .skills__procients {
    width: 100%;
  }
}
.skills__video h2,
.skills__procients h2 {
  font-size: 17px;
  margin-bottom: 70px;
}
@media (max-width: 600px) {
  .skills__video h2,
  .skills__procients h2 {
    font-size: 12px;
    margin-bottom: 30px;
  }
}
.skills__video p,
.skills__procients div {
  font-size: 13px;
}
@media (max-width: 600px) {
  .skills__video p,
  .skills__procients div {
    font-size: 10px;
  }
}
.skills__item {
  margin-bottom: 25px;
}
.skills__item-90,
.skills__item-80,
.skills__item-95,
.skills__item-65,
.skills__item-70 {
  background-repeat: no-repeat;
  background-position-y: bottom;
  background-size: contain;
  height: 35px;
  position: relative;
}
@media (max-width: 600px) {
  .skills__item-90,
  .skills__item-80,
  .skills__item-95,
  .skills__item-65,
  .skills__item-70 {
    height: 25px;
  }
}
.skills__item-90:after,
.skills__item-80:after,
.skills__item-95:after,
.skills__item-65:after,
.skills__item-70:after {
  width: 30px;
  height: 30px;
  top: -8px;
  position: absolute;
}
.skills__item-90 {
  background-image: url("../img/proc90.png");
}
.skills__item-90:after {
  content: "90%";
  right: 12%;
}
.skills__item-80 {
  background-image: url("../img/proc80.png");
}
.skills__item-80:after {
  content: "80%";
  right: 21%;
}
.skills__item-95 {
  background-image: url("../img/proc95.png");
}
.skills__item-95:after {
  content: "95%";
  right: 5%;
}
.skills__item-65 {
  background-image: url("../img/proc65.png");
}
.skills__item-65:after {
  content: "65%";
  right: 24%;
}
.skills__item-70 {
  background-image: url("../img/proc70.png");
}
.skills__item-70:after {
  content: "70%";
  right: 18%;
}
.main__title {
  font-size: 32px;
  margin-bottom: 20px;
  text-transform: uppercase;
}
.main__title > span {
  text-transform: none;
}
@media (max-width: 600px) {
  .main__title {
    font-size: 22px;
    margin-bottom: 10px;
  }
}
.main__text {
  font-size: 16px;
  color: #666666;
  letter-spacing: 5px;
  position: relative;
  max-width: max-content;
  margin: 0 auto;
  margin-bottom: 65px;
}
@media (max-width: 900px) {
  .main__text {
    max-width: 450px;
  }
}
@media (max-width: 600px) {
  .main__text {
    font-size: 12px;
    max-width: 200px;
    margin-bottom: 35px;
  }
}
.main__text::before,
.main__text::after {
  content: "";
  display: block;
  position: absolute;
  width: 20px;
  height: 1px;
  background-color: #666666;
  top: 55%;
}
.main__text::before {
  left: -10px;
  margin-left: -20px;
}
.main__text::after {
  right: -10px;
  margin-right: -20px;
}
.main__text--noline {
  margin-bottom: 100px;
}
.main__text--noline::before,
.main__text--noline::after {
  display: none;
}
.main-icon__box {
  width: 120px;
  height: 120px;
  background-color: #FFFFFF;
  border: 1px solid #eeeeee;
  transform: rotate(45deg);
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 12px 12px 0 0 #eeeeee;
  margin: 0;
  z-index: 2;
}
.main-icon__box--small {
  width: 50px;
  height: 50px;
  z-index: 0;
  box-shadow: 6px 6px 0 0 #eeeeee;
  margin: 0 auto;
}
@media (max-width: 950px) {
  .main-icon__box--small {
    height: 25px;
    width: 25px;
  }
}
@media (max-width: 600px) {
  .main-icon__box {
    height: 50px;
    width: 50px;
    box-shadow: 6px 6px 0 0 #eeeeee;
  }
  .main-icon__box--small {
    height: 25px;
    width: 25px;
  }
}
.main-icon__img {
  transform: rotate(315deg);
  display: flex;
  flex-direction: column;
  color: #000000;
  text-transform: uppercase;
  font-size: 11px;
}
@media (max-width: 600px) {
  .main-icon__img {
    font-size: 8px;
  }
}
.main-icon__fill path {
  fill: #000000;
}
@media (max-width: 600px) {
  .main-icon__fill {
    height: auto;
    width: 15px;
  }
}
.main-icon__cont {
  font-size: 28px;
  color: #000000;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 600px) {
  .main-icon__cont {
    font-size: 10px;
  }
}
.main-icon__wrap {
  height: 200px;
  width: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 600px) {
  .main-icon__wrap {
    height: 165px;
    width: 165px;
  }
}
.main-icon__text {
  margin-top: 75px;
  color: #000000;
}
@media (max-width: 600px) {
  .main-icon__text {
    margin-top: 0;
  }
}
.main__line::before {
  content: "";
  display: block;
  width: 50px;
  height: 1px;
  background-color: #FFFFFF;
  margin-bottom: 50px;
}
.main__line.main__line--grey::before {
  background-color: #666666;
  margin-bottom: 20px;
}
.main__line.main__line--light-gray::before {
  background-color: #eeeeee;
  margin-bottom: 20px;
}
.main__line-down::after {
  content: "";
  display: block;
  width: 35px;
  height: 1px;
  background-color: #eeeeee;
  margin-top: 20px;
}
.main__romb::after {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  background-color: #4187c8;
  position: absolute;
  transform: translateX(-50%) rotate(45deg);
  left: 50%;
  bottom: -8px;
}
.recentworks__item--active.main__romb.main__romb--small::after {
  background-color: #196fc0;
}
.main__romb.main__romb--onmap::after {
  background-color: #196fc0;
  width: 10px;
  height: 10px;
  left: 25%;
  top: 40%;
}
.soc-icons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 25px;
}
.soc-icons__icon {
  fill: #FFFFFF;
}
.soc-icons__icon--dig {
  width: 80px;
  height: 80px;
}
.soc-icons__icon:hover {
  fill: #69a1d6;
}
.services {
  text-align: center;
}
.services__list {
  list-style: none;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0;
  margin-bottom: 100px;
  position: relative;
}
@media (max-width: 600px) {
  .services__list {
    margin-bottom: 50px;
    justify-content: center;
  }
}
.services__item {
  width: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  cursor: pointer;
  /*  &:nth-last-child()::after{
    content: "";
    display: block;
    width: 42%;
    height: 1px;
    position: absolute;
    top: 32%;
    right: -70px;
    background-color: @color-grey;
  } */
}
.services__item:hover .main-icon__box {
  background-color: #196fc0;
  box-shadow: none;
  transition: 1s;
}
.services__item:hover .main-icon__cont {
  color: #FFFFFF;
}
.services__item:hover .main-icon__text {
  color: #196fc0;
}
.services__item:hover .about-icon1 {
  fill: #FFFFFF;
}
.services__list::after {
  content: "";
  display: block;
  width: 80%;
  height: 1px;
  position: absolute;
  top: 32%;
  right: 80px;
  background-color: #eeeeee;
}
@media (max-width: 1040px) {
  .services__list::after {
    top: 16%;
  }
}
@media (max-width: 640px) {
  .services__list::after {
    display: none;
  }
}
.cases {
  position: relative;
}
.cases .container {
  padding: 100px 20px 0;
}
@media (max-width: 600px) {
  .cases .container {
    padding: 50px 10px 0;
  }
}
.cases__list {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: center;
  margin-top: 70px;
}
@media (max-width: 900px) {
  .cases__list {
    margin-top: 20px;
  }
}
@media (max-width: 600px) {
  .cases__list {
    flex-wrap: wrap;
  }
}
.cases__list p {
  color: #000000;
  margin-top: 20px;
  margin-bottom: 0;
  font-size: 10px;
  text-align: center;
}
.cases__btns {
  margin: 0 auto;
  width: 200px;
  display: flex;
  justify-content: center;
}
.cases__button {
  border: 1px solid #666666;
  background-color: #FFFFFF;
  width: 25px;
  height: 25px;
  transform: rotate(45deg);
  margin: 10px;
  position: relative;
}
.cases__button--left::before {
  content: "";
  background-image: url("../img/btn-left-right.svg");
  background-repeat: no-repeat;
  background-position-y: center;
  background-position-x: center;
  background-size: auto;
  top: 2px;
  left: 1px;
  transform: rotate(315deg);
  width: 20px;
  height: 20px;
  display: block;
  position: absolute;
}
.cases__button--right::before {
  content: "";
  background-image: url("../img/btn-left-right.svg");
  background-repeat: no-repeat;
  background-position-y: center;
  background-position-x: center;
  background-size: auto;
  top: 0;
  left: 1px;
  transform: rotate(134deg);
  width: 20px;
  height: 20px;
  display: block;
  position: absolute;
}
.cases__wrap {
  display: none;
}
.cases__wrap.cases__wrap--active {
  display: block;
}
.cases__item {
  width: 17%;
  padding: 30px 20px 15px;
  border-bottom: 2px solid #196fc0;
  background-color: #FFFFFF;
  cursor: pointer!important;
}
@media (max-width: 600px) {
  .cases__item {
    width: 30%;
  }
}
.cases__item--active {
  background-color: #196fc0;
  position: relative;
}
.cases__item--active p {
  display: none;
}
.cases__item--active .cases__icon {
